import React from 'react';
import logo from './logo.svg';
import './App.css';



class App extends React.Component {
  constructor() {
    super();
    this.state = {
      apisLoaded: {
        ethTxs: true,
        erc20Txs: true,
      },
      isTxsPaneActive: true,
      ethTxs: [],
      erc20Txs: [],
      inputSearch: "",
    };
  }

  componentWillMount() {
    
  }

  fetch(address) {
    if (this.state.inputSearch !== "") {
      let apisLoaded = this.state.apisLoaded;
      apisLoaded.ethTxs = false;
      apisLoaded.erc20Txs = false;
      this.setState({ ethTxs: [], erc20Txs: [], apisLoaded: apisLoaded, inputSearch: "" });

      // fetchNormalTxs
     fetch(`https://api-ropsten.etherscan.io/api?module=account&action=txlist&address=${address}&startblock=0&endblock=999999999&page=1&offset=10&sort="desc"&apikey=${process.env.REACT_APP_ROPSTEN_API_KEY}`)
      .then(res => res.json())
      .then((result) => {
        //console.log(result);
        let apisLoaded = this.state.apisLoaded;
        apisLoaded.ethTxs = true;
        if (result.status === "1") {
          this.setState({ apisLoaded: apisLoaded, ethTxs: result.result, });
        } else {
          this.setState({ apisLoaded: apisLoaded, ethTxs: [], });
        }
      })
      .catch((error) => {
        let apisLoaded = this.state.apisLoaded;
        apisLoaded.ethTxs = true;
        this.setState({ ethTxs: [], erc20Txs: [], apisLoaded: apisLoaded, });
      });

      

      // fetchERC20Txs
      fetch(`https://api-ropsten.etherscan.io/api?module=account&action=tokentx&address=${address}&startblock=0&endblock=999999999&page=1&offset=10&sort="desc"&apikey=${process.env.REACT_APP_ROPSTEN_API_KEY}`)
      .then(res => res.json())
      .then((result) => {
        console.log(result);
        let apisLoaded = this.state.apisLoaded;
        apisLoaded.erc20Txs = true;
        if (result.status === "1") {
          this.setState({ apisLoaded: apisLoaded, erc20Txs: result.result, });
        } else {
          this.setState({ apisLoaded: apisLoaded, erc20Txs: [], });
        }
      })
      .catch((error) => {
        let apisLoaded = this.state.apisLoaded;
        apisLoaded.erc20Txs = true;
        this.setState({ ethTxs: [], erc20Txs: [], apisLoaded: apisLoaded, });
      });

      
      
    }
  }

  toggleTxsPane(bool) {
    this.setState({ isTxsPaneActive: bool });
  }

  ellipsify(string, n) {
  if (string.length > n) {
    return string.substring(0, n) + '...';
  }
    return string;
  }

  returnExactDecimal(n) {
    let nStr = n.toString();
    if (nStr.indexOf("e") > -1) {
      let nStrNoE = nStr.split("e").pop();
      return n.toFixed(Math.abs(Number(nStrNoE)));
    } else {
      return n;
    }
  }

  handleInputSearch(e) {
    this.setState({ inputSearch: e.target.value });
  }

  handleInputSearchKeyDown(e) {
    if (e.key === "Enter") {
      this.fetch(this.state.inputSearch);
    }
  }

  render() {
    let ethTxs = this.state.ethTxs;
    let erc20Txs = this.state.erc20Txs;
    let ethTxItem;
    let erc20TxItem;

    ethTxItem = ethTxs.map((n, i) => {
      return (
        <tr key={i}>
          <td><a href={"https://ropsten.etherscan.io/tx/" + n.hash} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(n.hash, 7) }</a></td>
          <td><a href={"https://ropsten.etherscan.io/block/" + n.blockNumber} className="App-link" target="_blank" rel="noreferrer">{ n.blockNumber }</a></td>
          <td><a href={"https://ropsten.etherscan.io/address/" + n.from} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(n.from, 7) }</a></td>
          <td><a href={"https://ropsten.etherscan.io/address/" + n.to} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(n.to, 7) }</a></td>
          <td>{ this.returnExactDecimal((Number(n.value) / 1e18)) + " ETH" }</td>
          <td>{ (Number(n.gas) * Number(n.gasPrice)) / 1e18 }</td>
        </tr>
      );
    });

    erc20TxItem = erc20Txs.map((e, i) => {
      return (
        <tr key={i}>
          <td><a href={"https://ropsten.etherscan.io/tx/" + e.hash} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(e.hash, 10) }</a></td>
          <td><a href={"https://ropsten.etherscan.io/address/" + e.from} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(e.from, 10) }</a></td>
          <td><a href={"https://ropsten.etherscan.io/address/" + e.to} className="App-link" target="_blank" rel="noreferrer">{ this.ellipsify(e.to, 10) }</a></td>
          <td>{ (Number(e.value) / 1e18) + " " + e.tokenSymbol }</td>
          <td>{ e.tokenName + " (" + e.tokenSymbol + ")" }</td>
        </tr>
      );
    });

    return (
      <div className="App">
        <header className="App-header">
          { (!this.state.apisLoaded.ethTxs && !this.state.apisLoaded.erc20Txs) ?
              <div>
                <img src={logo} className="App-logo" alt="logo" />
                <p>Loading...</p>
              </div>
            :
              <div>
                <table>
                  <tr>
                    <td><img src={logo} className="App-logo-sm" alt="logo" /></td>
                    <td><span><h3>React JS Ropsten Explorer</h3></span></td>
                  </tr>
                </table>
                <div className="search">
                  <input type="text" placeholder="Search Address.." onChange={(e) => {this.handleInputSearch(e)}} onKeyDown={(e) => {this.handleInputSearchKeyDown(e)}} />
                  <button onClick={() => this.fetch(this.state.inputSearch)}>Search</button>
                </div>
                <br/>
                <div>
                  { (this.state.isTxsPaneActive) ?
                      <div>
                        <span className="App-link">Transactions</span>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                        <span className="App-link-white" onClick={ () => this.toggleTxsPane(false) }>Erc20 Token Txns</span>
                      </div>
                    :
                      <div>
                        <span className="App-link-white" onClick={ () => this.toggleTxsPane(true) }>Transactions</span>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                        <span className="App-link">Erc20 Token Txns</span>
                      </div>
                  }
                </div>
                <br/>
                <div>
                  { (this.state.isTxsPaneActive) ?
                      <table className="transactions">
                        <tr>
                          <th>Txn Hash</th>
                          <th>Block</th>
                          <th>From</th>
                          <th>To</th>
                          <th>Value</th>
                          <th>Txn Fee</th>
                        </tr>
                        { ethTxItem }
                      </table>
                    :
                      <table className="transactions">
                        <tr>
                          <th>Txn Hash</th>
                          <th>From</th>
                          <th>To</th>
                          <th>Value</th>
                          <th>Token</th>
                        </tr>
                        { erc20TxItem }
                      </table>
                  }
                </div>
              </div>
          }
        </header>
      </div>
    );
  }
}

export default App;
